a {
  &.skip-content-x {
    position: absolute;
    width: 0;
    height: 0;
    padding: 0;
    margin: 0;
    overflow: hidden;
    border: none;
    color: $dark-gray;
    &:focus {
      width: auto;
      height: auto;
      padding: 9px 10px;
      border: 1px solid;
      display: block;
      top: 0;
      left: 0;
      z-index: 9999999;
      outline: none;
    }
  }
  &.external-link {
    position: relative;
    padding-right: 15px;
    &:after {
      position: absolute;
      font-family: "Glyphicons Halflings";
      content: "\e066";
      line-height: 1;
      @include x-rem(font-size, 12px);
      width: 12px;
      height: 12px;
      right: 0;
      top: 0;
      bottom: 0;
      margin: auto;
    }
  }
}

//Import all plugins
@import "plugins";
@import "kentico_form";
@import "animation";
#wrapper {
  max-width: 2560px;
  margin-left: auto;
  margin-right: auto;
}

//Sitefinity Admin panel fixes
[class*="sf-"] table,
.sfPageEditor table,
.rwTable,
.rdTable,
.sfWrapper,
.sfPageEditor.page,
.sfPageEditor {
  visibility: visible !important;
  ol,
  ul,
  table,
  iframe,
  a[href^="http"] {
    visibility: visible !important;
  }
}