.svg-Blue_Close {
  background: url("imgs/searchable-data-table/sprite01.svg") 92.88702928870293% 68.96551724137932% no-repeat;
  width: 11px;
  height: 12px;
}
.svg-chevron-left {
  background: url("imgs/searchable-data-table/sprite01.svg") 68.08510638297872% 70.17543859649123% no-repeat;
  width: 15px;
  height: 15px;
}
.svg-down-blue {
  background: url("imgs/searchable-data-table/sprite01.svg") 96.1864406779661% 54.23728813559322% no-repeat;
  width: 14px;
  height: 9px;
}
.svg-thin-tick {
  background: url("imgs/searchable-data-table/sprite01.svg") 35.56485355648535% 80.7909604519774% no-repeat;
  width: 11px;
  height: 9px;
}
.svg-thin-tick-white {
  background: url("imgs/searchable-data-table/sprite01.svg") 40.1673640167364% 80.7909604519774% no-repeat;
  width: 11px;
  height: 9px;
}
body.disallow-scroll {
  overflow: hidden;
  position: fixed;
}
.search-able-table {
  padding: 0 15px;
  .list-heading {
    margin: 0 15px;
    padding: 10px 15px;
    background-color: $dark-gray;
    color: $white;
    width: calc(100% - 30px);
  }
  .filter-row {
    display: flex;
    background-color: $white;
    @media screen and (max-width: $screen-xs) {
      display: block;
      position: fixed;
      top: 0;
      right: -100%;
      bottom: 0;
      left: auto;
      width: 80vw;
      height: 100vh;
      margin: auto;
      z-index: 2;
      @include mxTransition(all 0.3s ease-in-out);
    }
    + .filter-backdrop-overlay {
      display: none;
    }
    &.active {
      right: 0;
      + .filter-backdrop-overlay {
        display: block;
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 1;
        background-color: rgba(63,63,63,0.3);
        pointer-events: none;
      }
    }
  }
  .filter-wrap {
    margin-bottom: 11px;
    @media screen and (max-width: $screen-xs) {
      flex: 0 0 100%;
    }
    &.button-wrap {
      @media screen and (max-width: $screen-xs) {
        max-width: none;
        text-align: left;
      }
      @media (min-width: $screen-sm) and (max-width: $screen-md) {
        max-width: 182px;
      }
      button {
        width: 100%;
        @media screen and (max-width: $screen-xs) {
          margin-bottom: 15px;
        }
      }
    }
  }
  .filter-options {
    @media screen and (min-width: $screen-sm) {
      position: absolute;
      top: 50px;
      right: 0;
      bottom: auto;
      left: 0;
      z-index: 9;
      box-shadow: 0 5px 5px 5px rgba(186, 186, 186, 0.12);
    }
    background-color: $white;
    width: 100%;
    max-width: 100%;
    padding: 17px 20px;
    display: none;
    border: 1px solid transparent;
    .options-list {
      max-height: 224px;
      overflow: auto;
      &::-webkit-scrollbar {
        width: 5px;
        margin-right: 5px;
        border-right: 5px solid transparent;
      }
      /* Track */
      &::-webkit-scrollbar-track {
        border-radius: 2px;
      }
      /* Handle */
      &::-webkit-scrollbar-thumb {
        background: $dark-gray;
        border-radius: 2px;
      }
      /* Handle on hover */
      &::-webkit-scrollbar-thumb:hover {
        background: $dark-gray;
      }
      > * {
        &.hide-me {
          display: none !important;
        }
      }
      .styled-checkbox {
        &:last-of-type {
          label {
            margin-bottom: 0;
          }
        }
      }
    }
    .styled-checkbox {
      input:focus,
      input:hover {
        + label {
          background-color: $dim-gray;
          color: $white;
        }
      }
    }
  }
  .filter-set {
    position: relative;
    .filter-title {
      border: 1px solid $silver;
      border-radius: 6px;
      background-color: $white;
      box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.05);
      font-size: 1rem;
      font-weight: 300;
      line-height: 17px;
      color: $black;
      position: relative;
      @include placeHolderTextColor($black);
      &:hover,
      &:focus {
        text-decoration: none;
        border: 1px solid rgba(37, 61, 134, 0.5);
        box-shadow: 0 0 2px 1px rgba(32, 96, 173, 0.15);
      }
      display: block;
      padding: 15px 12px 16px;
      &:hover,
      &:focus,
      &:active {
        outline: none;
        text-decoration: none;
      }
      &:after {
        content: "";
        @extend .svg-down-blue;
        top: 0;
        right: 14px;
        bottom: 0;
        left: auto;
        display: block;
        margin: auto;
        position: absolute;
      }
      &.selected {
        color: $dark-gray;
      }
    }
    .clear-filter {
      position: absolute;
      top: -8px;
      right: 3px;
      bottom: 0;
      left: auto;
      z-index: 2;
      display: none;
      margin: auto;
      height: 36px;
      width: 36px;
      padding: 2px 12px;
      border-radius: 6px;
      background-color: $white;
      &:before {
        display: block;
        position: inherit;
        top: 2px;
        right: 0;
        bottom: 0;
        left: 0;
        margin: auto;
        content: "";
        @extend .svg-Blue_Close;
      }
      &.active {
        display: block;
      }
    }
    &.active {
      display: block;
      .filter-title {
        display: block;
      }
      .filter-options {
        display: block;
      }
      .clear-filter {
        top: 1px;
        bottom: auto;
      }
      &.multi-select {
        .filter-title:after {
          transform: rotate(180deg);
        }
      }
    }
    &.multi-select {
      .clear-filter {
        top: 0;
      }
      &.active {
        .clear-filter {
          top: 7px;
        }
      }
    }
  }
  .search-table {
    visibility: visible;
    @media screen and (max-width: $screen-xs) {
      display: block;
      border: none;
      margin-bottom: 22px;
      thead {
        display: none !important;
      }
      tbody {
        display: block;
        tr {
          display: flex;
          flex-wrap: wrap;
          padding: 11px 15px 9px;
          @media screen and (max-width: $screen-xs) {
            padding: 20px 15px 9px;
          }
          td {
            border: none;
            flex: 0 0 33.33333%;
            max-width: 33.33333%;
            padding: 3px 0;
            @media screen and (max-width: $screen-xs) {
              padding: 0;
            }
            &:nth-of-type(1) {
              > span.cell-value {
                position: relative;
                margin-bottom: 5px;
                padding-bottom: 5px;
              }
              > span.cell-value:before {
                content: "";
                position: absolute;
                top: auto;
                right: 0;
                bottom: -11px;
                left: 0;
                display: block;
                margin: auto;
                width: calc(100vw - 60px);
                height: 1px;
                border-bottom: 1px solid $dark-gray;
              }
            }
            &:nth-child(1n+4) > span.cell-label {
              margin-top: 22px;
            }
            &:nth-child(1n+4) > span.cell-value {
              margin-bottom: 10px;
            }
          }
        }
      }
    }
    tbody tr td {
      > span {
        display: block;
        font-size: inherit;
        line-height: inherit;
        @media screen and (max-width: $screen-xs) {
          @include x-rem(font-size, 12px);
          text-align: left;
          line-height: 14px;
        }
        &.cell-label {
          font-weight: bold;
          color: $dark-gray;
          display: none;
          @media screen and (max-width: $screen-xs) {
            display: block;
            min-height: 28px;
          }
        }
      }
    }
  }
  .mobile-filters {
    padding: 15px;
    background-color: $white-smoke;
    height: 100vh;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.11);
    > div.row {
      margin-top: 30px;
    }
    .filter-back {
      display: none;
      position: relative;
      padding-left: 11px;
      &:before {
        content: "";
        @extend .svg-chevron-left;
        position: absolute;
        top: 0;
        right: auto;
        bottom: 0;
        left: 0;
        display: block;
        margin: auto;
      }
    }
    .filter-lbl {
      display: inline-block;
      color: $dark-gray;
      font-size: 1.125rem;
      @include x-rem(font-size, 16px);
      font-weight: bold;
      line-height: 21px;
      margin-bottom: 16px;
    }
    .filter-close {
      color: $dark-gray;
      font-size: 0.75rem;
      line-height: 14px;
      text-align: right;
    }
    .button-box {
      text-align: center;
      padding: 15px;
      .btn {
        width: 100%;
        margin-bottom: 6px;
      }
      .group-1 {
        display: block;
        a {
          color: $dark-gray;
          @include x-rem(font-size, 12px);
          line-height: 14px;
        }
      }
      .group-2 {
        display: none;
      }
    }
    .filters-box {
      padding: 0;
      background-color: $white;
      max-height: calc(100vh - 180px);
      overflow: scroll;
    }
    .filter-box {
      border-top: 1px solid $silver;
      position: relative;
      .box-title {
        display: block;
        padding: 7px 15px;
        background-color: $white;
        span {
          display: inline-block;
          font-size: 0.75rem;
          line-height: 14px;
        }
        span.title {
          color: $black;
        }
        span.selection {
          float: right;
          margin-top: 6px;
          text-align: right;
          color: #909090;
        }
        span.clear-btn {
          transform: scale(0.85);
          vertical-align: middle;
        }
        text-decoration: none;
        &:hover,
        &:focus,
        &:active {
          text-decoration: none;
        }
      }
      .box-items {
        clear: both;
        display: none;
        .custom-checkbox {
          border-top: 1px solid $silver;
          position: relative;
          &:first-of-type {
            border-top: none;
          }
          label {
            margin: 0;
            padding: 13px 25px 12px 15px;
            color: $black;
            font-size: 0.75rem;
            line-height: 14px;
            font-weight: 400;
            width: 100%;
          }
          input[type="radio"],
          input[type="checkbox"]{
            width: 0;
            height: 0;
            opacity: 0;
            position: absolute;
            &:checked {
              + label {
                color: $dark-gray;
                font-weight: bold;
                position: relative;
                &:after {
                  content: "";
                  @extend .svg-thin-tick;
                  position: absolute;
                  display: block;
                  top: 0;
                  right: 15px;
                  bottom: 0;
                  left: auto;
                  margin: auto;
                }
              }
              &:focus {
                & + label {
                  background-color: $white;
                }
              }
            }
          }
        }
      }
      .text-filter {
        padding: 15px 15px;
      }
      &.filtered {
        .box-title {
          padding: 7px 30px 7px 15px;
          span.selection {
            color: $dark-gray;
          }
          span.clear-btn {
            position: absolute;
            right: 15px;
            top: 14px;
          }
        }
      }
    }
    &.expanded {
      .filter-back {
        display: inline-block;
      }
      .button-box {
        .group-2 {
          display: block;
        }
        .group-1 {
          display: none;
        }
      }
      .filters-box .filter-box {
        display: none !important;
        &.show-items {
          display: block !important;
          .box-title {
            display: none;
          }
          .box-items {
            display: block !important;
          }
        }
      }
    }
  }
  .filter-options{
    padding: 5px 0;
    border-radius: 4px;
    box-shadow: 0 1px 4px 0 rgba(0,0,0,0.22);
    .styled-checkbox {
      position: relative;
      &:hover,
      &:focus,
      &:active {
        background-color: $dim-gray;
        label {
          color: $white;
        }
      }
      label {
        margin: 0;
        padding: 6px 15px;
        color: $black;
        font-size: 0.8125rem;
        line-height: 18px;
        width: 100%;
        padding-left: 20px;
      }
      input[type="checkbox"]{
        opacity: 0;
        position: absolute;
        &:checked {
          + label {
            color: $dark-gray;
            position: relative;
            &:after {
              content: "";
              @extend .svg-thin-tick;
              position: absolute;
              display: block;
              top: 0;
              left: 6px;
              bottom: 0;
              right: auto;
              margin: auto;
            }
          }
          &:focus {
            + label {
              background-color: $white;
            }
          }
          &:hover,
          &:focus,
          &:active {
            + label {
              color: $white;
              background-color: $dim-gray;
              &:after {
                @extend .svg-thin-tick-white;
              }
            }
          }
        }
      }
    }
  }
}
.filter-app {
  h1 {
    border-bottom: 2px solid;
    padding-bottom: 14px;
    margin-bottom: 18px;
    @media screen and (max-width: $screen-xs) {
      display: none;
    }
  }
  .filter-count {
    span {
      @include x-rem(font-size, 13px);
      line-height: 14px;
      margin-top: 4px;
    }
  }
}
@import "easy_dropdown";